<template>
  <div id="smog" :style="{
  'background': `url('static/smogBg/${bgImg}.jpg') no-repeat 0 0 / cover fixed`}
">
    <div class="inner">
      <div class="position" v-if="smogCity!==''">
        <span><i class="icon-position"></i></span> {{smogCity}}, {{province}}, 中国
        <input @keydown.enter="searchKeyDown"
               type="text"
               ref="searchInput"
               autofocus="autofocus"
               v-if="searchShow"
               placeholder="请输入您想查找的城市">
        <i class="icon-search" @click="search"></i>
      </div>
      <dl class="content">
        <dt>
          <h2 :style="{'color': smogColor}">{{weatherList.num}}</h2>
          <em v-if="smogCity!==''" :style="{'color': smogColor}">{{bgImg}}</em>
        </dt>
        <!--<dd>aaaa</dd>-->
        <!--<dd>bbbb</dd>-->
        <!--<dd>cccc</dd>-->
        <dd class="ac text_hide">
          <span>今日雾霾提示</span> <span>{{tips}}</span>
        </dd>
      </dl>
      <div class="wrapper">
        <div class="more_weather">
          <ul class="weather_list">
            <li class="list_item">
              <div class="item text_hide">AQI指数</div>
              <div class="item text_hide">空气质量</div>
              <div class="item text_hide">PM2.5</div>
              <div class="item text_hide">PM10</div>
              <div class="item text_hide">CO</div>
              <div class="item text_hide">NO<sub>2</sub></div>
              <div class="item text_hide">SO<sub>2</sub></div>
              <div class="item text_hide">O<sub>3</sub></div>
            </li>
            <li class="list_num">
              <div class="item" v-html="weatherList.num===null||''?'暂无':weatherList.num"></div>
              <div class="item" v-html="weatherList.status===null||''?'暂无':weatherList.status"></div>
              <div class="item" v-html="weatherList.pm25===null||''?'暂无':weatherList.pm25"></div>
              <div class="item" v-html="weatherList.pm10===null||''?'暂无':weatherList.pm10"></div>
              <div class="item" v-html="weatherList.co===null||''?'暂无':weatherList.co"></div>
              <div class="item" v-html="weatherList.no2===null||''?'暂无':weatherList.no2"></div>
              <div class="item" v-html="weatherList.so2===null||''?'暂无':weatherList.so2"></div>
              <div class="item" v-html="weatherList.o3===null||''?'暂无':weatherList.o3"></div>
            </li>
          </ul>
        </div>
        <div class="rank">
          <h4 class="text_hide">雾霾统计概况</h4>
          <ul v-if="rank.length">
            <Li>
              <p><span>月最高AQI</span> : {{rank[0]}} (出现在{{rank[1]}})</p>
            </Li>
            <Li>
              <p><span>月最低AQI</span> : {{rank[2]}} (出现在{{rank[3]}})</p>
            </Li>
          </ul>
        </div>
      </div>
      <div class="canvas_wrapper"
           @touchstart="canvasTouchStart"
           @touchmove="canvasTouchMove"
           @touchend="canvasTouchEnd">
        <div class="canvas" ref="canvas">
          <line-chart :rowData="list" :colData="colList"></line-chart>
        </div>
      </div>
      <foot-box></foot-box>
      <loading v-if="!list.length && !error"></loading>
      <wrong v-if="error"></wrong>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import { ERROR } from 'api/config'
  import { getSmogList } from 'api/getSmogList'
  import { getSmogType } from 'common/js/getType'
  import { mapGetters, mapMutations } from 'vuex'

  import LineChart from 'base/lineChart/lineChart'
  import FootBox from 'components/footer/footer'
  import Loading from 'base/loading/loading'
  import Wrong from 'base/error/error'

  export default {
    data () {
      return {
        error: false,
        province: '',
        searchShow: false,
        weatherList: {},
        list: [],
        colList: [],
        smogColor: '#fff',
        bgImg: '中',
        tips: '',
        rank: [],
        deltaX: 0
      }
    },
    created () {
      this.touch = {}
    },
    computed: {
      ...mapGetters([
        'smogCity'
      ])
    },
    methods: {
      canvasTouchStart (ev) {
        this.touch.initiated = true
        const touch = ev.touches[0]
        this.touch.startX = touch.pageX + Math.abs(this.deltaX)
      },
      canvasTouchMove (ev) {
        if (!this.touch.initiated) {
          return
        }
        const touch = ev.touches[0]
        this.deltaX = touch.pageX - this.touch.startX
        if (this.deltaX >= 0) {
          this.deltaX = 0
        }
        if (this.deltaX <= -(this.$refs.canvas.offsetWidth - this.$refs.canvas.parentNode.offsetWidth)) {
          this.deltaX = -(this.$refs.canvas.offsetWidth - this.$refs.canvas.parentNode.offsetWidth)
        }
        this.$refs.canvas.style.webkitTransform = `translateX(${this.deltaX}px)`
      },
      canvasTouchEnd (ev) {
        this.touch.initiated = false
      },
      _getWeatherList (city = '') {
        getSmogList(city).then((res) => {
          console.log(res.data)
          if (res.data.error === ERROR) {
            this.weatherList = res.data.data
            this.list = res.data.list
            this.colList = res.data.day
            this.rank = res.data.time
            this.setSmogCity(res.data.data.cityname)
            this.province = res.data.data.provincename
            this.smogColor = getSmogType(res.data.data.num).smogColor
            this.bgImg = getSmogType(res.data.data.num).bgImg
            this.tips = getSmogType(res.data.data.num).tips
            this.error = false
          } else {
            this.error = true
          }
        }).catch((res) => {
          this.error = true
        })
      },
      search () {
        if (this.searchShow) {
          let city = this.$refs.searchInput.value
          if (city === '') {
            this.searchShow = false
            return
          }
          if (city === this.smogCity) {
            alert('和当前城市相同,请重新输入!')
            this.searchShow = false
            return
          }
          this.setSmogCity(city)
          this._getWeatherList(city)
          city = ''
          this.searchShow = false
        } else {
          this.searchShow = true
        }
      },
      searchKeyDown () {
        let city = this.$refs.searchInput.value
        if (city === this.smogCity) {
          alert('和当前城市相同,请重新输入!')
          this.searchShow = false
          return
        }
        this.setSmogCity(city)
        this._getWeatherList(city)
        city = ''
        this.searchShow = false
      },
      ...mapMutations({
        setSmogCity: 'SET_SMOG_CITY'
      })
    },
    mounted () {
      this._getWeatherList(this.smogCity)
    },
    components: {
      LineChart,
      FootBox,
      Loading,
      Wrong
    }
  }
</script>

<style lang="less" scoped rel="stylesheet/less">
  @import "../../common/css/mixin.less";

  @media screen and(max-width: 450px) {
    #smog {
      .inner {
        width: 95% !important;
        .wrapper {
          display: block !important;
          .more_weather {
            width: 100%;
            box-sizing: border-box;
            ul {
              li {
                display: block;
                height: fit-content !important;
                .item {
                  height: 45px;
                }
              }
              .list_item {
                text-align: left;
                float: left;
                border: none;
              }
              .list_num {
                text-align: right;
              }
            }
          }
          .rank {
            width: 100%;
            line-height: 2;
          }
        }
        .position {
          input {
            width: 6rem !important;
          }
        }
        .canvas_wrapper {
          .canvas {
            width: 1000px;
          }
        }
      }
    }
  }

  #smog {
    color: #fff;
    min-height: 100%;
    padding-top: 100px;
    padding-bottom: 20px;
    box-sizing: border-box;
    transition: all 2s;
    .position {
      width: fit-content;
      padding: 0 20px;
      margin: 10px 0;
      border-radius: 15px;
      height: 30px;
      line-height: 30px;
      color: #fff;
      font-size: 18px;
      background-color: rgba(0, 0, 0, .5);
      transition: all 0.3s;
      span {
        padding-right: 20px;
        font-size: 20px;
        color: rgb(255, 90, 60)
      }
      input {
        height: 20px;
        width: 150px;
        background-color: transparent;
        border: none;
        color: #ddd;
        outline: #ddd;
        border-bottom: 1px solid #ccc;
      }
    }
    .content {
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;
      dt {
        width: 100%;
        h2 {
          display: inline-block;
          position: relative;
          font: 120px/ 120px Arial;
          height: 120px;
          span {
            position: absolute;
            top: 10px;
            right: -20px;
            display: block;
            box-sizing: border-box;
            width: 15px;
            height: 15px;
            border: 4px solid #fff;
            border-radius: 50%;
          }
        }
        em {
          margin-left: 10px;
          font: 30px / 30px '楷体';
        }
      }
      dd {
        height: 26px;
        line-height: 26px;
        margin-right: 20px;
        display: flex;
        flex-wrap: nowrap;
        &.ac {
          width: 100%;
        }
        span {
          .background;
          display: inline-block;
          padding: 0 15px;
          border-radius: 13px;
        }
      }
    }
    .wrapper {
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-wrap: nowrap;
      .more_weather {
        .background;
        width: 70%;
        margin-top: 10px;
        padding: 10px;
        h6 {
          height: 30px;
          font: 16px / 30px '微软雅黑';
        }
        .weather_list {
          width: 100%;
          li {
            height: 60px;
            font: 16px / 60px '宋体';
            border-bottom: 1px solid rgba(255, 255, 255, .5);
            display: flex;
            justify-content: space-between;
            flex-wrap: nowrap;
            text-align: center;
            &:last-of-type {
              border: none;
            }
            .item {
              flex: 1;
              font: 16px / 60px '宋体';
            }
          }
        }
      }
      .rank {
        .background;
        margin-top: 10px;
        width: 27%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        h4 {
          font: 20px / 40px '';
          text-align: center;
        }
        ul {
          width: fit-content;
          position: relative;
          left: 0;
          right: 0;
          margin: 0 auto;
        }
      }
    }
    .canvas_wrapper {
      width: 100%;
      overflow: hidden;
      .canvas {
        position: relative;
        margin-top: 30px;
        width: 100%;
        height: 300px;
      }
    }
  }
</style>



